export default {
  name: "Icon",
  props: {
    name: {
      type: String,
      default: ''
    },
    size: {
      type: Number,
      default: 32
    },
    color: {
      type: String,
      default: ''
    },
    bold: {
      type: Boolean,
      default: false
    },
    visible: {
      type: Boolean,
      default: true
    },
    index: {
      type: Number,
      default: 0
    }
  },
  methods: {
    handleClick (index) {
      this.$emit('click', {
        index
      })
    }
  }
}
